import BookQRPdf from "../book/BookQRPdf";
import StaffQRPdf from "../book/StaffQRPdf";
import StudentQRPdf from "../book/StudentQRPdf";
onmessage = (message) => {
  let args = message.data;
  let printResult = "";
  if (args?.isType === "STUDENT") {
    printResult = StudentQRPdf(args?.all_student);
  } else if (args?.isType === "STAFF") {
    printResult = StaffQRPdf(args?.all_staff);
  } else {
    printResult = BookQRPdf(args?.book_qr);
  }
  postMessage(printResult);
};
