import jsPDF from "jspdf";
const StaffQRPdf = (staffList) => {
  var doc = new jsPDF({
    format: "a4",
    putOnlyUsedFonts: true,
  });
  doc.setFont("WorkSans");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(8);
  let y = 3;
  let x = 9;
  let imageWidth = 30;
  let imageHeight = 30;
  let staff = staffList ?? [];
  for (let i = 0; i < staff?.length; i++) {
    if (i === 0) {
    } else if (i % 6 === 0) {
      x = 9;
      y += 30 + 10;
    } else {
      x += 30 + 3;
    }
    if (y + 30 > 290) {
      doc.addPage({
        format: "a4",
        putOnlyUsedFonts: true,
      });
      y = 3;
    }
    if (staff[i]?.library_qr_code) {
      doc.addImage(
        `${staff[i]?.library_qr_code}`,
        x,
        y,
        imageWidth,
        imageHeight,
        undefined,
        "FAST"
      );
    }
    let staffName = `${staff[i]?.staffFirstName ?? ""} ${
      staff[i]?.staffMiddleName ? `${staff[i]?.staffMiddleName} ` : ""
    }${staff[i]?.staffLastName ?? ""}`;
    let split_name = doc.splitTextToSize(staffName, 30);
    doc.text(split_name, x + 2, y + imageHeight + 2);
  }

  return doc.output("bloburl", {
    filename: `library-staff-qr.pdf`,
  });
};

export default StaffQRPdf;
