import jsPDF from "jspdf";
const BookQRPdf = (bookList) => {
  var doc = new jsPDF({
    format: "a4",
    putOnlyUsedFonts: true,
  });
  // var width = doc.internal.pageSize.getWidth();
  // var height = doc.internal.pageSize.getHeight();

  doc.setFont("WorkSans");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(8);

  // console.log(height);
  let y = 3;
  let x = 9;
  let imageWidth = 30;
  let imageHeight = 30;
  // let book = [
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  //   ...bookList,
  // ];
  // let book = bookList?.slice(0, 2990) ?? [];
  let book = bookList ?? [];
  for (let i = 0; i < book?.length; i++) {
    if (i === 0) {
    } else if (i % 6 === 0) {
      x = 9;
      y += 30 + 10;
    } else {
      x += 30 + 3;
    }
    if (y + 30 > 290) {
      doc.addPage({
        format: "a4",
        putOnlyUsedFonts: true,
      });
      y = 3;
    }
    if (book[i]?.book_qr_code) {
      doc.addImage(
        `${book[i]?.book_qr_code}`,
        x,
        y,
        imageWidth,
        imageHeight,
        undefined,
        "FAST"
      );
    }

    // doc.addImage(
    //   `${book[i]?.book_qr_code}`,
    //   "PNG",
    //   x,
    //   y,
    //   imageWidth,
    //   imageHeight
    // );

    if (book[i]?.accession_number)
      doc.text(book[i]?.accession_number ?? "N/A", x + 2, y + imageHeight + 2);
    else doc.text(book[i]?.qviple_book_id ?? "N/A", x + 2, y + imageHeight + 2);
    // doc.text(book[i]?.accession_number ?? "N/A", x + 2, y + imageHeight + 5);
  }
  return doc.output("bloburl", {
    filename: `library-book-qr.pdf`,
  });
};

export default BookQRPdf;

