import jsPDF from "jspdf";
const StudentQRPdf = (studentList) => {
  var doc = new jsPDF({
    format: "a4",
    putOnlyUsedFonts: true,
  });
  doc.setFont("WorkSans");
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(8);
  let y = 3;
  let x = 9;
  let imageWidth = 30;
  let imageHeight = 30;
  let student = studentList ?? [];
  for (let i = 0; i < student?.length; i++) {
    if (i === 0) {
    } else if (i % 6 === 0) {
      x = 9;
      y += 30 + 10;
    } else {
      x += 30 + 3;
    }
    if (y + 30 > 290) {
      doc.addPage({
        format: "a4",
        putOnlyUsedFonts: true,
      });
      y = 3;
    }
    if (student[i]?.library_qr_code) {
      doc.addImage(
        `${student[i]?.library_qr_code}`,
        x,
        y,
        imageWidth,
        imageHeight,
        undefined,
        "FAST"
      );
    }
    let studentName = `${student[i]?.studentFirstName ?? ""} ${
      student[i]?.studentMiddleName ? `${student[i]?.studentMiddleName} ` : ""
    }${student[i]?.studentLastName ?? ""}`;
    let split_name = doc.splitTextToSize(studentName, 30);
    doc.text(split_name, x + 2, y + imageHeight + 2);
  }
  return doc.output("bloburl", {
    filename: `library-student-qr.pdf`,
  });
};

export default StudentQRPdf;
